<template>
    <div class="login-form">
        <h3>
            
        </h3>
        <hr/>
        <Row>
            <Col><span class="label"></span></Col>
            <Col>
            <Input v-model="email" type="text" style="width: 300px;"/>
            </Col>
        </Row>
        <Row style="margin-top: 5px;">
            <Col><span class="label"></span></Col>
            <Col>
            <Input v-model="password" type="password" style="width: 300px"/>
            </Col>
        </Row>
        <Row style="margin-top: 5px;">
            <Col><span class="label"></span></Col>
            <Col>
            <Input v-model="password_repeat" type="password" style="width: 300px"/>
            </Col>
        </Row>
        <Row style="margin-top: 15px;">
            <Col>
            <Button type="primary" style="width: 300px" @click="login">Register</Button>
            </Col>
        </Row>
    </div>
</template>

<script>
import user from "@/api/user.js";
import identity from "@/api/identity.js";
import validator from "validator";

export default {
  name: "login",
  data() {
    return {
      email: "",
      password: "",
      password_repeat: ""
    };
  },
  methods: {
    async login() {
      let msg = null;
      if (validator.isEmail(this.email) === false) msg = "";

      if (this.password.length < 6 || this.password.length > 18)
        msg = "6, 18";
      if (this.password != this.password_repeat) msg = "";

      if (msg)
        return this.$Notice.error({
          title: msg
        });

      try {
        await user.Register(this.email, this.password);
        let ret = await user.Login(this.email, this.password);
        identity.set(ret);
        this.$router.push('/')
      } catch (err) {
        console.log(err);
        this.$Notice.error({
          title: "Failed to register"
        });
      }
    }
  }
};
</script>

<style scoped>
.login-form {
  width: 400px;
  margin: 30px auto 100px;
}
.login-form h3 {
  font-weight: 300;
  font-size: 18px;
}
.login-form hr {
  margin-bottom: 50px;
  margin-left: 2px;
  width: 300px;
  background-color: lightgray;
}

.label {
  color: gray;
  font-weight: 300;
  font-size: 16px;
}
</style>


